<template>
  <div style="">
    <div class="reward-layout">
      <img src="../assets/img/header1.d9a6e40.png" alt="" class="header-img" />
      <div class="report-content">
        <main class="reward-main">
          <div class="main-top">
            <div class="title-bg"></div>
            <div class="top-table">
              <div class="table-tr">
                <div class="table-td">我的推广码</div>
                <div class="table-td">今日新增注册</div>
                <div class="table-td">今日新增充值</div>
              </div>
              <div class="table-main">
                <div class="table-td">{{ userInfo.code }}</div>
                <div class="table-td">{{ userInfo.regisCount }}人</div>
                <div class="table-td">{{ userInfo.czCount }}人</div>
              </div>
              <div class="table-tr">
                <div class="table-td">团队人数</div>
                <div class="table-td">今日总收益</div>
                <div class="table-td">今日个人收益</div>
              </div>
              <div class="table-main">
                <div class="table-td">{{ userInfo.childNum + 1 }}人</div>
                <div class="table-td">
                  {{ userInfo.syAmountTotal && userInfo.syAmountTotal }}元
                </div>
                <div class="table-td">
                  {{ userInfo.syAmountGr && userInfo.syAmountGr }}元
                </div>
              </div>
            </div>
            <img
              src="data:image/png;base64,"
              alt=""
              class="l-side"
            />
            <img
              src="data:image/png;base64,"
              alt=""
              class="r-side"
            />
          </div>

          <div class="main-bottom main-top">
            <div class="title-bg fxfs"></div>
            <div class="copy-wrapper">
              <p class="dl">代理链接</p>
              <div class="copy">
                <input
                  autocomplete="new-password"
                  type="text"
                  readonly="readonly"
                  class="input"
                  v-model="appInfo.andriod_url"
                />
                <div
                  class="btn-copy tag-read"
                  :data-clipboard-text="appInfo.andriod_url"
                  @click="copy"
                >
                  复制链接
                </div>
              </div>
            </div>
            <div class="copy-wrapper">
              <p class="dl">邀请链接</p>
              <div class="dl-info">
                复制上方链接发送给好友，注册成功后即可成为您的下级会员，同时您可获得下级所有会员每天总反水30%作为代理佣金！
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from "clipboard";
import { dailiDetail, sysApp } from "../api/public";
export default {
  data() {
    return {
      appInfo: {},
      userInfo: {},
    };
  },
  mounted() {
    this.userInfo = JSON.parse(localStorage.getItem("user_info"));
    this.getUser();
    this.getAppInfo();
  },
  methods: {
    copy() {
      var clipboard = new Clipboard(".tag-read");
      clipboard.on("success", () => {
        this.$message.success("复制成功"); //这里你如果引入了elementui的提示就可以用，没有就注释即可
        // 释放内存
        clipboard.destroy();
      });
      clipboard.on("error", () => {
        // 不支持复制
        console.log("该浏览器不支持自动复制");
        // 释放内存
        clipboard.destroy();
      });
    },
    getAppInfo() {
      sysApp({}).then((res) => {
        if (res.code == 1) {
          this.appInfo = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    getUser() {
      dailiDetail({
        id: this.userInfo.id,
        dateFlag: 0,
        user_name: this.userInfo.user_name,
      }).then((res) => {
        if (res.code == 1) {
          this.userInfo = { ...this.userInfo, ...res.data };
        } else {
          this.$message.error(res.msg);
        }
      });
    },
  },
};
</script>

<style scoped>
